import React from 'react';
import { Carousel, Button } from 'antd';
import { injectIntl } from 'react-intl';
import config from './../../config';

import 'containers/Process/index.css';
import './index.css';

const Cover = ({ messages }) => (
  <section className="cover">
    <div className="container-fluid container-mw-xl">
      <div className="row">
        <div className="col-md-6 col-lg-5">
          <Carousel autoplay>
            {Object.keys(messages.landing[config.default]).map(key => {
              let short = messages.landing[config.default][key];
              return (
                <div className="text-carousel" key={key}>
                  <h1>
                    <span className="bold">{short.text1}</span>
                  </h1>
                  <p className="lead">{short.text2}</p>
                  <div className="divider divider-short border-primary my-4"></div>
                  <p>{short.text3}</p>
                </div>
              );
            })}
          </Carousel>

          <a href="http://prod.pangeamt.com/info" target="_blank" rel="noopener noreferrer">
            <Button type="primary" size="large">
              {messages.default.getStarted}
            </Button>
          </a>
        </div>
      </div>
    </div>
  </section>
);

const LandingComponent = props => {
  const {
    intl: { messages },
  } = props;
  return <Cover messages={messages} />;
};
export default injectIntl(LandingComponent);
